.story {
    overflow: auto;

    figure {
        margin: 0;
    }

    &__thumb {
        border-radius: 3px;
        overflow: hidden;
        background: #E9ECEF url(../img/background_ver3.png) center center/60% no-repeat;

        a {
            display: flex;
            width: 100%;
            height: 100%;
            transition: transform .5s ease-out .1s;
            transition: 0.5s;
            color: $primary;
        }

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: transform 0.5s;
            text-indent: -9999px;
        }

        // &:hover {
        //     overflow: hidden;

        //     img {
        //         -webkit-transform: scale($rate-zoom);
        //         /* Safari and Chrome */
        //         -moz-transform: scale($rate-zoom);
        //         /* Firefox */
        //         -ms-transform: scale($rate-zoom);
        //         /* IE 9 */
        //         -o-transform: scale($rate-zoom);
        //         /* Opera */
        //         transform: scale($rate-zoom);
        //     }
        // }

    }

    &__heading a {
        display: block;
        transition: 0.5s;
        overflow: hidden;

        &:hover {
            color: #e33a3c !important;
        }
    }

    time {
        display: block;
    }

    &__meta {
        a {
            &:hover {
                color: #e33a3c;
            }
        }
    }
}